export default {
	name: "tuiRate",
	props: {
		//数量
		quantity: {
			type: Number,
			default: 5
		},
		//当前选中
		current: {
			type: Number,
			default: 0
		},
		//当前选中星星分数(大于0，小于等于1的数)
		score: {
			type: [Number, String],
			default: 1
		},
		//禁用点击
		disabled: {
			type: Boolean,
			default: false
		},
		//大小
		size: {
			type: Number,
			default: 30
		},
		//未选中颜色
		normal: {
			type: String,
			default: "#b2b2b2"
		},
		//选中颜色
		active: {
			type: String,
			default: "#e41f19"
		},
		//未选中是否为空心
		hollow: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			pageX: 0,
			percent: 0
		};
	},
	created() {
		this.percent = Number(this.score || 0) * 100
	},
	watch: {
		score(newVal, oldVal) {
			this.percent = Number(newVal || 0) * 100
		}
	},
	methods: {
		handleTap(e) {
			if (this.disabled) {
				return;
			}
			const index = e.currentTarget.dataset.index;
			this.$emit('change', {
				index: Number(index) + 1,
				score:0
			})
		},
		touchMove(e) {
			if (this.disabled) {
				return;
			}
			if (!e.changedTouches[0]) {
				return;
			}
			const movePageX = e.changedTouches[0].pageX;
			const distance = movePageX - this.pageX;

			if (distance <= 0) {
				return;
			}
			let single = distance / this.size
			let index = this.$_.floor(single)
			let half = this.$_.subtract(single,index)>=0.5?0.5:0
			// let index = Math.ceil(distance / this.size);
			// index = index > this.count ? this.count : index;
			this.$emit('change', {
				index: index,
				score:half
			})
		}
	},










	onReady() {
		const className = '.tui-rate-box';
		let query = uni.createSelectorQuery().in(this)
		query.select(className).boundingClientRect((res) => {
			this.pageX = res.left || 0
		}).exec()
	}
}
